body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar h1 {
  margin: 0;
}

.navbar .links a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.navbar .links a:hover {
  text-decoration: underline;
}

.footer {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  text-align: center;
}

.content {
  padding: 20px;
}

.home, .projects, .about, .contact {
  max-width: 800px;
  margin: 0 auto;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;
}

.project {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 5px;
}

.contact form {
  display: flex;
  flex-direction: column;
}

.contact label {
  margin-bottom: 10px;
}

.contact input, .contact textarea {
  padding: 8px;
  margin-top: 5px;
}

.contact button {
  padding: 10px;
  background-color: #333;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-top: 10px;
}

.contact button:hover {
  background-color: #555;
}
